import projectApi from "@/api/project";
import { setItem } from "@/helpers/persistanceStorage";

const state = {
  data: null,
  isLoading: null,
  currentProject: null,
  allProject: null,
  error: null,
};

export const mutationTypes = {
  getProjectStart: "[project] Get project start",
  getProjectSuccess: "[project] Get project success",
  getProjectFailure: "[project] Get project failure",

  NewProjectStart: "[project] New project start",
  NewProjectSuccess: "[project] New project success",
  NewProjectFailure: "[project] New project failure",

  deleteProjectStart: "[project] Delete project start",
  deleteProjectSuccess: "[project] Delete project success",
  deleteProjectFailure: "[project] Delete project failure",

  editingProjectStart: "[project] Editing project start",
  editingProjectSuccess: "[project] Editing project success",
  editingProjectFailure: "[project] Editing project failure",
};
export const actionTypes = {
  getProject: "[project] Get Project",
  newProject: "[project] New Project",
  deleteProject: "[project] Delete Project",
  editingProject: "[project] Editing Project",
};

const mutations = {
  [mutationTypes.getProjectStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.getProjectSuccess](state, payload) {
    state.isLoading = false;
    state.currentProject = payload;
    state.allProject = payload;
    state.data = payload;
  },
  [mutationTypes.getProjectFailure](state) {
    state.isLoading = false;
  },
  [mutationTypes.NewProjectStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.NewProjectSuccess](state, payload) {
    state.isLoading = false;

    state.data = payload;
  },
  [mutationTypes.NewProjectFailure](state) {
    state.isLoading = false;
  },
  [mutationTypes.deleteProjectStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.deleteProjectSuccess](state) {
    state.isLoading = false;
    // state.data = payload;
  },
  [mutationTypes.deleteProjectFailure](state) {
    state.isLoading = false;
  },
  [mutationTypes.editingProjectStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.editingProjectSuccess](state, payload) {
    state.isLoading = false;
    state.data = payload;
  },
  [mutationTypes.editingProjectFailure](state) {
    state.isLoading = false;
  },
};
export const getterTypes = {
  currentProject: "[project] currentProject",
};
const getters = {
  [getterTypes.currentProject]: (state) => (project_id) =>
    state.currentProject.find((t) => t.project_id === project_id),
};
const actions = {
  [actionTypes.getProject](context, token) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.getProjectStart);
      projectApi
        .getProject(token)
        .then((response) => {
          context.commit(mutationTypes.getProjectSuccess, response.data);
          window.localStorage.setItem("currentProject", response.data);
          setItem("currentProject", response.data); //для работы с объектами и массивами
          resolve(response.data);
        })
        .catch(() => {
          context.commit(mutationTypes.getProjectFailure);
        });
    });
  },
  [actionTypes.newProject](context, name) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.NewProjectStart);
      projectApi
        .NewProject(name, console.log(name))
        .then((response) => {
          context.commit(mutationTypes.NewProjectSuccess, response.data);
          resolve(response.data);
        })
        .catch(() => {
          context.commit(mutationTypes.NewProjectFailure);
        });
    });
  },
  [actionTypes.deleteProject](context, apiUrl) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.deleteProjectStart);
      console.log(apiUrl);
      projectApi
        .deleteProject(apiUrl, console.log(apiUrl))
        .then(() => {
          context.commit(mutationTypes.deleteProjectSuccess);
          resolve();
        })
        .catch(() => {
          context.commit(mutationTypes.deleteProjectFailure);
        });
    });
  },
  [actionTypes.editingProject](context, name) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.editingProjectStart);
      projectApi
        .editingProject(name)
        .then(() => {
          context.commit(mutationTypes.editingProjectSuccess);
          resolve();
        })
        .catch(() => {
          context.commit(mutationTypes.editingProjectFailure);
        });
    });
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
