import axios from "@/api/axios";

const getErrors = (apiUrl) => {
  console.log(apiUrl, "запрос");
  return axios.get(`/api/v1/errors/${apiUrl}`);
};
const getCurrentError = (apiUrl) => {
  console.log(apiUrl, "запрос");
  return axios.get(`/api/v1/errors/${apiUrl.baseUrl}/${apiUrl.baseId}`);
};
const delError = (apiUrl) => {
  console.log(apiUrl, "запрос");
  return axios.delete(`/api/v1/errors/${apiUrl.baseUrl}/${apiUrl.baseId}`);
};
const delErrorsList = (array) => {
  console.log(array.error_ids, "запрос  на удаление списка");
  return axios.delete(`/api/v1/errors/${array.project}`, {
    data: {
      error_ids: array.error_ids,
    },
  });
};

export default {
  getErrors,
  getCurrentError,
  delError,
  delErrorsList,
};
