<template>
  <div class="create-project">
    <h1 class="main-h1">Editing</h1>
    <p>
      Текущее название: <span> {{ this.currentProject.name }}</span>
    </p>
    <form @submit.prevent="onSubmit" class="form">
      <fieldset class="form-group">
        <input
          type="text"
          class="form-control project-create"
          placeholder="Project Name"
          name="projectname"
          v-model="name"
        />
        <!-- v-bind:value="CONTACTID.name" -->
      </fieldset>

      <button class="btn-edit">
        Editing
      </button>
    </form>
  </div>
</template>

<script>
import { actionTypes } from "@/store/modules/project";
import { getterTypes } from "@/store/modules/project";
export default {
  name: "AppEditingProject",
  data() {
    return {
      name: "",
    };
  },
  computed: {
    baseUrl() {
      return this.$route.params.slug;
    },

    currentProject() {
      return this.$store.getters[getterTypes.currentProject](
        this.$route.params.slug
      );
    },
  },
  methods: {
    onSubmit() {
      this.$store
        .dispatch(actionTypes.editingProject, {
          baseUrl: this.baseUrl,
          name: this.name,
        })
        .then(() => {
          console.log("ответ получен");
          this.$router.push({ name: "home" });
        })
        .catch(() => {
          console.log("Ошибка");
        });
    },
  },
};
</script>

