<template>
  <div id="app">
    <Header />
    <router-view />
  </div>
</template>
<script>
import Header from "@/components/header.vue";
import { mapState } from "vuex";
// import { mapGetters } from "vuex";
import { actionTypes } from "@/store/modules/project";
import { actionTypesAuth } from "@/store/modules/auth";

export default {
  name: "App",
  data() {
    return {
      TOKEN: "",
    };
  },
  components: {
    Header,
  },
  computed: {
    ...mapState({
      currentUser: (state) => state.auth.currentUser,
      currentProject: (state) => state.project.currentProject,
    }),
  },

  methods: {
    getProject() {
      this.TOKEN = "Bearer " + this.currentUser.user_token.token;
      console.log(this.currentUser.user_token.token);
      this.$store.dispatch(actionTypes.getProject, this.TOKEN).then(() => {
        console.log("successful project");
      });
    },
  },
  mounted() {
    this.$store
      .dispatch(actionTypesAuth.getCurrentUser)
      .then(() => {
        this.getProject();
      })
      .catch(() => {});
  },
};
</script>
