import axios from "@/api/axios";
import { getItem } from "@/helpers/persistanceStorage";
// const getFeed = (apiUrl) => {
//   return axios.get(apiUrl);
// };
const token = getItem("accessToken");

const config = {
  headers: { Authorization: `${token}` },
};

const getProject = () => {
  return axios.get("api/v1/project/", config);
};

const NewProject = (name) => {
  return axios.post("api/v1/project/", {
    name: name,
  });
};

const deleteProject = (apiUrl) => {
  console.log(apiUrl, "запрос");
  return axios.delete(`api/v1/project/${apiUrl}`);
};
const editingProject = (name) => {
  return axios.put(`api/v1/project/${name.baseUrl}`, { name: name.name });
};

export default {
  // getFeed,
  getProject,
  NewProject,
  deleteProject,
  editingProject,
  config,
};
