<template>
  <div class="create-project">
    <h1 class="main-h1">New Project</h1>
    <form @submit.prevent="onSubmit" class="form">
      <fieldset class="form-group">
        <input
          type="text"
          class="form-control project-create"
          placeholder="Project Name"
          name="projectname"
          v-model="name"
        />
      </fieldset>

      <button class="btn-edit">
        Create
      </button>
    </form>
  </div>
</template>

<script>
import { actionTypes } from "@/store/modules/project";
export default {
  name: "AppCreateProject",
  data() {
    return {
      name: "",
    };
  },
  methods: {
    onSubmit() {
      this.$store
        .dispatch(actionTypes.newProject, this.name)
        .then(() => {
          console.log("ответ получен");
          this.$router.push({ name: "home" });
        })
        .catch(() => {
          console.log("Ошибка");
        });
    },
  },
};
</script>

