import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/Home.vue";
import Login from "@/views/Login";
import Register from "@/views/Register";
import CreateProject from "@/views/Create-Project";
import EditingProject from "@/views/Editing-Project";
import Errors from "@/views/Errors";
import ErrorItem from "@/views/Error-item";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
    meta: {
      Auth: true,
    },
  },
  {
    path: "/login",
    name: "login",
    component: Login,
    meta: {
      Auth: false,
    },
  },
  {
    path: "/register",
    name: "register",
    component: Register,
    meta: {
      Auth: false,
    },
  },
  {
    path: "/new-project",
    name: "newProject",
    component: CreateProject,
    meta: {
      Auth: true,
    },
  },
  // {
  //   path: "/editing-project",
  //   name: "editingProject",
  //   component: EditingProject,
  // },
  {
    path: "/project/:slug",
    name: "editingProject",
    component: EditingProject,
    meta: {
      Auth: true,
    },
  },
  {
    path: "/errors/:slug",
    name: "errors",
    component: Errors,
    meta: {
      Auth: true,
    },
  },
  {
    path: "/errors/:slug/:id",
    name: "error-item",
    component: ErrorItem,
    meta: {
      Auth: true,
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
