<template>
  <div class="main-container">
    <h1 class="main-h1">Projects</h1>
    <div class="" v-if="isLoading">Loading...</div>
    <div class="project" v-if="currentProject">
      <div class="project-list">
        <ProjectList
          v-for="project in currentProject"
          :key="project.id"
          @updata="updata()"
          v-bind:project_item="project"
        />
      </div>
    </div>
  </div>
</template>

<script>
import ProjectList from "@/components/Project-list.vue";
import { actionTypes } from "@/store/modules/project";
import { mapState } from "vuex";

export default {
  name: "AppProject",
  data() {
    return {
      TOKEN: "",
    };
  },
  computed: {
    ...mapState({
      isLoading: (state) => state.project.isLoading,
      currentUser: (state) => state.auth.currentUser,
      currentProject: (state) => state.project.currentProject,
    }),
  },
  components: {
    ProjectList,
  },
  methods: {
    updata() {
      this.TOKEN = "Bearer " + this.currentUser.user_token.token;
      console.log(this.currentUser.user_token.token);
      this.$store.dispatch(actionTypes.getProject, this.TOKEN).then(() => {
        console.log("successful project");
      });
    },
  },
  mounted() {
    this.updata();
  },
};
</script>
