<template>
  <article class="project-block">
    <div class="project-block__header">
      <h1 @click="project_delete(project_item)">X</h1>
    </div>
    <div class="project-block__body">
      <div class="project-block__name">
        <router-link
          :to="{
            name: 'errors',
            params: { slug: project_item.project_id },
          }"
          class="preview-link"
        >
          <p>{{ project_item.name }}</p>
        </router-link>
      </div>
      <p class="project-block__count-error">
        <span>{{ project_item.count_error }}</span> error
      </p>
      <p class="project-block__count-transactions">
        <span>{{ project_item.count_transaction }}</span> transactions
      </p>
    </div>
    <div class="project-block__button">
      <router-link
        :to="{
          name: 'editingProject',
          params: { slug: project_item.project_id },
        }"
        class="preview-link"
      >
        <button class="btn-grad" @click="editing(project_item)">
          Editing
        </button>
      </router-link>
    </div>
  </article>
</template>

<script>
import { actionTypes } from "@/store/modules/project";
export default {
  name: "AppProjectList",
  data() {
    return {
      str: "",
    };
  },
  props: {
    project_item: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  methods: {
    editing(proj) {
      console.log(proj);
    },
    project_delete(project) {
      this.str = project.project_id;
      this.$store
        .dispatch(actionTypes.deleteProject, this.str)
        .then(() => {
          this.$emit("updata");
          console.log("Успешно удалено");
        })
        .catch(() => {
          console.log("Ошибка удаления");
        });
    },
  },
};
</script>

