<template>
  <div class="error-block" v-if="currentProject_item">
    <div class="error-block__select">
      <input
        type="checkbox"
        v-model="checked"
        :value="error_item.id_error"
        @change="onChange()"
      />
    </div>
    <div class="error-block__info">
      <router-link
        :to="{
          name: 'error-item',
          params: {
            slug: currentProject_item.project_id,
            id: error_item.id_error,
          },
        }"
        class="preview-link"
      >
        <div class="error__type">
          {{ error_item.type_error }}
        </div>
      </router-link>
      <div class="error__description">
        {{ error_item.description }}
      </div>
      <div class="error__info-block">
        <div class="error__name-project">
          {{ currentProject_item.name }}
        </div>
        <div class="error__time">
          {{ formatDate(new Date(error_item.date_time)) }}
        </div>
        <div class="error__time-ago">{{ nowDay }} days ago</div>
      </div>
    </div>
  </div>
</template>

<script>
// import { getterTypes } from "@/store/modules/project";
// import { mapState } from "vuex";
export default {
  name: "AppErrorItem",
  data() {
    return {
      //НОВОЙ МЕТОД
      checkedP: false,

      checkboxValue: [],
    };
  },
  props: {
    error_item: {
      type: Object,
      default() {
        return {};
      },
    },

    currentProject_item: {
      type: Object,
      default() {
        return {};
      },
    },
    idx: {
      type: Number,
      default() {
        return {};
      },
    },
    //новые
    value: {
      type: Array,
      default() {
        return {};
      },
    },
  },
  computed: {
    baseUrl() {
      return this.$route.params.slug;
    },
    nowDay() {
      let date1 = new Date();
      let date2 = new Date(this.error_item.date_time);
      // Рассчитать разницу во времени двух дат
      let Difference_In_Time = date1.getTime() - date2.getTime();
      // Для расчета нет. дней между двумя датами
      let Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24);
      return Math.floor(Difference_In_Days);
    },
    checked: {
      get() {
        return this.value;
      },
      set(val) {
        this.checkedP = val;
      },
    },
  },
  methods: {
    formatDate(date) {
      return (
        date.getFullYear() +
        "." +
        (date.getMonth() + 1) +
        "." +
        date.getDate() +
        "  " +
        date.getHours() +
        ":" +
        date.getMinutes() +
        ":" +
        date.getSeconds()
      );
    },
    //текущий
    onChange() {
      this.$emit("input", this.checkedP);
    },
  },
};
</script>

