import errorsApi from "@/api/errors";

const state = {
  data: null,
  isLoading: false,
  currentError: null,
  error: null,
};

export const mutationTypes = {
  getErrorsStart: "[errors] Get errors start",
  getErrorsSuccess: "[errors] Get errors success",
  getErrorsFailure: "[errors] Get errors failure",

  delErrorStart: "[errors] Del errors start",
  delErrorSuccess: "[errors] Del errors success",
  delErrorFailure: "[errors] Del errors failure",

  delErrorsListStart: "[errors] Del errors list start",
  delErrorsListSuccess: "[errors] Del errors list success",
  delErrorsListFailure: "[errors] Del errors list failure",

  getCurrentErrorStart: "[errors] Get current errors start",
  getCurrentErrorSuccess: "[errors] Get current errors success",
  getCurrentErrorFailure: "[errors] Get current errors failure",
};
export const actionTypes = {
  getErrors: "[errors] Get Errors",
  delError: "[errors] Delele Error",
  delErrorsList: "[errors] Delele list Errors",
  getCurrentError: "[errors] Get current Errors",
};

const mutations = {
  [mutationTypes.getErrorsStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.getErrorsSuccess](state, payload) {
    state.isLoading = false;
    state.data = payload;
  },
  [mutationTypes.getErrorsFailure](state) {
    state.isLoading = false;
  },
  [mutationTypes.delErrorStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.delErrorSuccess](state, payload) {
    state.isLoading = false;
    state.data = payload;
  },
  [mutationTypes.delErrorFailure](state) {
    state.isLoading = false;
  },
  [mutationTypes.delErrorsListStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.delErrorsListSuccess](state, payload) {
    state.isLoading = false;
    state.data = payload;
  },
  [mutationTypes.delErrorsListFailure](state) {
    state.isLoading = false;
  },
  [mutationTypes.getCurrentErrorStart](state) {
    state.isLoading = true;
    state.data = null;
  },
  [mutationTypes.getCurrentErrorSuccess](state, payload) {
    state.isLoading = false;
    state.currentError = payload;
    state.data = payload;
  },
  [mutationTypes.getCurrentErrorFailure](state) {
    state.isLoading = false;
  },
};

const actions = {
  [actionTypes.getErrors](context, url) {
    return new Promise((resolve) => {
      console.log(url);
      context.commit(mutationTypes.getErrorsStart);
      errorsApi
        .getErrors(url)
        .then((response) => {
          context.commit(mutationTypes.getErrorsSuccess, response.data);
          resolve(response.data);
        })
        .catch(() => {
          context.commit(mutationTypes.getErrorsFailure);
        });
    });
  },
  [actionTypes.getCurrentError](context, url) {
    return new Promise((resolve) => {
      console.log(url);
      context.commit(mutationTypes.getCurrentErrorStart);
      errorsApi
        .getCurrentError(url)
        .then((response) => {
          context.commit(mutationTypes.getCurrentErrorSuccess, response.data);
          resolve(response.data);
        })
        .catch(() => {
          context.commit(mutationTypes.getCurrentErrorFailure);
        });
    });
  },
  [actionTypes.delError](context, url) {
    return new Promise((resolve) => {
      console.log(url);
      context.commit(mutationTypes.delErrorStart);
      errorsApi
        .delError(url)
        .then((response) => {
          context.commit(mutationTypes.delErrorSuccess, response.data);
          resolve(response.data);
        })
        .catch(() => {
          context.commit(mutationTypes.delErrorFailure);
        });
    });
  },
  [actionTypes.delErrorsList](context, array) {
    return new Promise((resolve) => {
      console.log(array);
      context.commit(mutationTypes.delErrorsListStart);
      errorsApi
        .delErrorsList(array)
        .then((response) => {
          context.commit(mutationTypes.delErrorsListSuccess, response.data);
          resolve(response.data);
        })
        .catch(() => {
          context.commit(mutationTypes.delErrorsListFailure);
        });
    });
  },
};

export default {
  state,
  actions,
  mutations,
};
