<template>
  <div class="main-container">
    <div class="breadcrumbs">
      <router-link
        :to="{
          name: 'errors',
          params: { slug: this.baseUrl },
        }"
        class="preview-link"
      >
        Назад
      </router-link>
    </div>
    <div
      class="error-item"
      v-for="currentError in currentError"
      :key="currentError.id_error"
    >
      <div class="error-item__error-type">
        {{ currentError.type_error }}
      </div>
      <div class="error-item__description">
        {{ currentError.description }}
      </div>
      <button class="btn-edit" @click="deleteError()">Удалить ошибку</button>
      <div class="program-code">
        <div class="program-code__name">
          <p>
            {{ currentError.name_py }} in
            <span
              >{{ currentError.name_function }} at line
              {{ currentError.code_of_line }}</span
            >
          </p>
        </div>
        <div class="program-code__block">
          <div
            class="program-code__console"
            v-for="code in currentError.program_code"
            :key="code.id"
            v-bind:class="{
              activeError: currentError.code_of_line === code.count_str,
            }"
          >
            <div class="program-code__str">
              <p>{{ code.count_str }}</p>
            </div>
            <div class="program-code__value">
              <p v-html="code.value"></p>
            </div>
          </div>
        </div>
      </div>
      <div class="program-code__args" v-if="currentError.args !== '{}'">
        <div class="args">
          <div class="args__title">Args</div>
          <div class="args__data">{{ currentError.args }}</div>
        </div>
      </div>
      <div class="program-code__args" v-if="currentError.kwargs !== '{}'">
        <div class="args">
          <div class="args__title">Kwargs</div>
          <div class="args__data">{{ currentError.kwargs }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { actionTypes } from "@/store/modules/errors";
import { mapState } from "vuex";
export default {
  name: "AppErrorItem",
  computed: {
    baseUrl() {
      return this.$route.params.slug;
    },
    ...mapState({
      isLoading: (state) => state.errors.isLoadind,
      currentError: (state) => state.errors.currentError,
    }),
    baseId() {
      return this.$route.params.id;
    },
  },
  methods: {
    deleteError() {
      this.$store
        .dispatch(actionTypes.delError, {
          project: this.baseUrl,
          error_ids: this.checked,
        })
        .then(() => {
          this.getProject();
          this.checked = [];
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
  },
  mounted() {
    this.$store
      .dispatch(actionTypes.getCurrentError, {
        baseUrl: this.baseUrl,
        baseId: this.baseId,
      })
      .then((resp) => {
        console.log(resp, "currentError");
      })
      .catch(() => {
        console.log("Ошибка");
      });
  },
};
</script>

