import axios from "@/api/axios";

const register = (credentials) => {
  return axios.post("/sign-up", credentials);
};
const login = (credentials) => {
  return axios.post("/auth", credentials);
};
const getCurrentUser = () => {
  return axios.get("api/v1/user/");
};
export default {
  register,
  login,
  getCurrentUser,
};
