<template>
  <div class="main-container">
    <div class="breadcrumbs">
      <router-link
        :to="{
          name: 'home',
        }"
        class="preview-link"
      >
        Назад
      </router-link>
    </div>
    <div class="" v-if="isLoadingErr">Loading...</div>
    <div class="error" v-if="errors">
      <h1 class="main-h1">Issues ({{ countErrors }})</h1>
      <div class="error__check-all">
        <input type="checkbox" @click="checkAll()" v-model="isCheckAll" /> Check
        All
      </div>

      <div class="errors-table">
        <div class="toolbar" v-if="this.checked.length">
          <div class="toolbar__flex">
            <p>Выбрано {{ checked.length }}</p>
            <button class="btn-del" @click="deleteError()">
              Удалить ошибки
            </button>
          </div>
        </div>
        <div class="errors-table__header"></div>
        <div
          class="errors-table__body"
          v-bind:class="{ tool: this.checked.length }"
        >
          <error-list
            v-for="(error, index) in errors"
            :key="error.id"
            :error_item="error"
            :idx="index"
            :currentProject_item="currentProjectId"
            v-model="checked"
          >
          </error-list>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { getterTypes } from "@/store/modules/project";
import ErrorList from "@/components/Error-list.vue";
import { actionTypes } from "@/store/modules/errors";
import { getterTypes } from "@/store/modules/project";
import { mapState } from "vuex";

export default {
  name: "AppErrors",
  data() {
    return {
      checked: [],
      countErrors: "",
      arrayError: [],
      isCheckAll: false,
    };
  },
  components: {
    ErrorList,
  },
  computed: {
    baseUrl() {
      return this.$route.params.slug;
    },
    ...mapState({
      isLoading: (state) => state.project.isLoading,
      isLoadingErr: (state) => state.errors.isLoading,
      currentUser: (state) => state.auth.currentUser,
      currentProject: (state) => state.project.currentProject,
      errors: (state) => state.errors.data,
    }),
    currentProjectId() {
      return this.currentProject
        ? this.$store.getters[getterTypes.currentProject](this.baseUrl)
        : null;
    },
  },
  methods: {
    checkAll() {
      this.isCheckAll = !this.isCheckAll;
      this.checked = [];
      if (this.isCheckAll) {
        this.errors.forEach((error) => {
          this.checked.push(error.id_error);
        });
      }
      // if (this.isCheckAll) {
      //   // Check all
      //   for (var key in this.errors) {
      //     this.checked.push(this.errors[key]);
      //   }
      // }
    },
    deleteError() {
      this.$store
        .dispatch(actionTypes.delErrorsList, {
          project: this.baseUrl,
          error_ids: this.checked,
        })
        .then(() => {
          this.getProject();
          this.checked = [];
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
    getProject() {
      this.$store
        .dispatch(actionTypes.getErrors, this.baseUrl)
        .then(() => {
          this.countErrors = this.errors.length;
          // this.currentProjects();
        })
        .catch((er) => {
          console.log("Ошибка", er);
        });
    },
  },
  mounted() {
    this.getProject();
  },
};
</script>

