<template>
  <div class="container-login">
    <div class="register">
      <h1 class="title-h1">Sing Up</h1>
      <p class="register__question">
        <router-link :to="{ name: 'login' }">Have an account?</router-link>
      </p>
      <AppValidationErrors
        v-if="validationErrors"
        :validation-errors="validationErrors"
      />
      <form @submit.prevent="onSubmit" class="form">
        <fieldset class="form-group">
          <input
            type="text"
            class="form-control"
            placeholder="Username"
            v-model="name"
          />
        </fieldset>
        <fieldset class="form-group">
          <input
            type="text"
            class="form-control"
            placeholder="Email"
            v-model="email"
          />
        </fieldset>
        <fieldset class="form-group">
          <input
            type="password"
            class="form-control"
            placeholder="Password"
            v-model="password"
          />
        </fieldset>
        <button class="btn" :disabled="isSubmitting">
          Sing Up
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import AppValidationErrors from "@/components/ValidationErrors";
import { actionTypesAuth } from "@/store/modules/auth";
import { mapState } from "vuex";
export default {
  name: "AppRegiter",
  data() {
    return {
      email: "",
      password: "",
      name: "",
    };
  },
  components: {
    AppValidationErrors,
  },
  computed: {
    ...mapState({
      isSubmitting: (state) => state.auth.isSubmitting,
      validationErrors: (state) => state.auth.validationErrors,
    }),
  },
  methods: {
    onSubmit() {
      console.log("Submit");

      this.$store
        .dispatch(actionTypesAuth.register, {
          email: this.email,
          name: this.name,
          password: this.password,
        })
        .then((user) => {
          console.log("successful", user);
          this.$router.push({ name: "home" });
        });
    },
  },
};
</script>

<style></style>
